import React, { useEffect, useRef } from 'react';
import './InvitationText.css';  // CSS  

const InvitationText = () => {
  const textRef = useRef(null);

  // AOS 
  useEffect(() => {
    import('aos').then((AOS) => {
      AOS.init();
    });
  }, []);

  return (
    <div
      ref={textRef} // ref 
      data-aos="fade-up" 
      data-aos-duration="1500"
    >
      <h2>INVITATION</h2>
      <p>     </p>
      <p>   </p>
      <p>     </p>
      <p className="extra-space">      </p>
      <p>     </p>
      <div className="names">
        <p> .   </p>
        <p> .   </p>
      </div>
    </div>
  );
};

export default InvitationText;
