import React, { useEffect } from 'react';
import './KakaoMap.css'; // KakaoMap.css  .

const KakaoMap = () => {
    useEffect(() => {
        const loadKakaoMap = () => {
            if (!window.kakao) {
                console.error("Kakao Maps SDK not loaded");
                return;
            }

            const container = document.getElementById('map');
            const options = {
                center: new window.kakao.maps.LatLng(35.8728, 128.6025),
                level: 5,
            };

            const map = new window.kakao.maps.Map(container, options);
            const markerPosition = new window.kakao.maps.LatLng(35.8728, 128.6025);
            const marker = new window.kakao.maps.Marker({
                position: markerPosition,
            });
            marker.setMap(map);
        };

        const script = document.createElement('script');
        script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=7c83ad9e898fc41e0bf2bffb14232074&autoload=false`;
        script.async = true;
        script.onload = () => {
            window.kakao.maps.load(loadKakaoMap);
        };
        script.onerror = () => {
            console.error("Failed to load the Kakao Maps SDK");
        };
        document.head.appendChild(script);

        return () => {
            document.head.removeChild(script);
        };
    }, []);

    return (
        <div>
            <h2 className="location-title">LOCATION</h2>
            <h3 className="location-subtitle">   (22)    119</h3>
            <div className="map-container" id="map"></div>

            <div className="directions">
                <h3 className="directions-title"> </h3>
                <div className="transportation">
                    <h4></h4>
                    <p>:   </p>
                    <p>:    106  </p>

                    <h4></h4>
                    <p>172 ( )</p>
                    <p>        5</p>

                    <p>405 ( )</p>
                    <p>        5</p>

                    <p>472 ( )</p>
                    <p>    5</p>

                    <h4></h4>
                    <p>1 ()</p>
                    <p> 11   10   5</p>

                    <p>2 ()</p>
                    <p> 4   10   5</p>

                    <h4></h4>
                    <p>   :  3 </p>
                    <p>    .</p>
                </div>
            </div>
        </div>
    );
};

export default KakaoMap;
